import React from 'react'
import { Input, Button, Icon, Modal, message, DatePicker, Tag } from 'antd'
import { getMineList, getTodoList, getDoneList, getProcessImg } from '/api/process'
import Common from '/page/common.jsx'
import ApplyForm from '/page/process/applyForm.jsx'
const ButtonGroup = Button.Group
const { RangePicker } = DatePicker


class Apply extends Common{

	async componentWillMount () {
		let pathname=this.props.location.pathname
		console.log(pathname)
		if(pathname.indexOf('applymine') > 0){
			this.setState({auth: false, searchFun: getMineList})
		}else if(pathname.indexOf('applytodo') > 0){
			this.setState({auth: true, searchFun: getTodoList})
		}else {
			this.setState({auth: false, searchFun: getDoneList})
		}
		this.search()
	}

	state = Object.assign({}, this.state, {
		auth: false,
		searchFun: '',
		search: Object.assign({condition: ''}, this.state.pageconf),
		columns: [{
			title: '状态',
			dataIndex: 'status',
			render: t => {
				if (t == '审核中'){
					return <Tag color="blue">{t}</Tag>
				}else if (t == '已拒绝'){
					return <Tag color="volcano">{t}</Tag>	
				}else{
					return <Tag color="lime">{t}</Tag>
				}
			}
		},{
			title: '停留时间',
			dataIndex: 'waitTime'
		},{
			title: '流程标题',
			dataIndex: 'instanceName'
		},{
			title: '提交人',
			dataIndex: 'createUser'
		},{
			title: '提交时间',
			dataIndex: 'createTime'
		},{
			title: '当前节点',
			dataIndex: 'taskName'
		},{
			title: '流程图',
			dataIndex: 'process-pic',
			render: (t, r) => r.status == '审核中' ? <Icon type="eye" onClick={async _ => {this.showProcessPic(r)}} /> : null
		},{
			title: ' 操作 ',
			dataIndex: 'operate',
			render: (t, r) => <a style={{display: 'inline-block', width: 60}} onClick={async _ => {this.openForm(r)}}>查看</a>
		}],
		selectedtable: false,
		loading: true,
		tabledata: [],
		modalconf: {visible: false, item: {}},
		picmodelConf: {visible: false, item: {}},
		processimg: {visible: false, src: null}
	})


	search = async () => {
		await this.setState({loading: true})
		let search = Object.assign({}, this.state.search)
		this.handleTime(search, 'startDate')
		this.handleTime(search, 'endDate')
		return this.state.searchFun(search)
		.then(res => {
			let data = (f => f(f))(f => list => list.map(val => {
				let baseItem = Object.assign({}, val, { key: val.id })
				return baseItem
			}))(res.data.records)
			this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: res.data.total,
					current: res.data.current
				})
			})
		})
	}
  //查看流程图
	showProcessPic = r => {
		getProcessImg({id: r.id}).then(res => {
      var url = URL.createObjectURL(res);
      //document.getElementById('image').src = url;
      console.log(url)
      console.log("received some data");
      this.setState({processimg: {visible: true, src: url}})
		})
	}

	openForm = r => {
		this.setState({modalconf: {visible: true, title: r.instanceName, item: r}})
	}

	done = _ => {
		this.search()
	}

	renderSearch = _ => <div>
		<div className="mgrSearchBar">
			<RangePicker 
			onChange={dates => this.changeSearch({startDate: dates[0], endDate: dates[1]})}
			value={[this.state.search.startDate, this.state.search.endDate]}
			className="search"
			placeholder={['开始日期', '结束日期']} />
			<Input 
			value={this.state.search.condition}
			suffix={this.state.search.condition ? <Icon 
			type="close-circle" 
			onClick={_ => this.changeSearch({condition: ''})} /> : null}
			onChange={e => this.changeSearch({condition: e.target.value})}
			addonBefore="关键字" placeholder="流程标题/申请人" />
			<Button 
			onClick={this.search}
			type="primary" icon="search">搜索</Button>
		</div>
	</div>

	renderBtn = _ => <div>

	</div>

	rendermodal = _ => [
	<ApplyForm config={this.state.modalconf} 
		auth={this.state.auth}
		onCancel={_ => this.cancelform('modalconf')}
		done={this.done}  />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>,<Modal title="流程图"
		  visible={this.state.processimg.visible}
		  footer={null}
		  mask={true}
		  width={800}
		  onCancel={_ => this.setState({processimg:{visible: false}})}
		>
			<img src={this.state.processimg.src} />
		</Modal>]

}

export default Apply


